import Link from "next/link";
import InteractiveNavigation from "@/components/client/InteractiveNavigation";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";

export default function Navigation() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userRole, setUserRole] = useState("user");

  useEffect(() => {
    supabase.auth.getUser().then(({ data }) => {
      if (data.user) {
        setIsAuthenticated(true);
        // Optionally, set userRole from user metadata if available
        // setUserRole(data.user.role || "user");
      } else {
        setIsAuthenticated(false);
      }
    });
    // Optionally, subscribe to auth changes for real-time updates
    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        setIsAuthenticated(!!session?.user);
      }
    );
    return () => {
      listener?.subscription.unsubscribe();
    };
  }, []);

  const publicLinks = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
    { href: "/contact", label: "Contact" },
  ];

  const userLinks = [
    { href: "/dashboard", label: "Dashboard" },
    { href: "/profile", label: "Profile" },
    { href: "/settings", label: "Settings" },
  ];

  const adminLinks = [{ href: "/admin", label: "Admin Panel" }];

  return (
    <nav className="bg-white/95 backdrop-blur-sm border-b border-slate-200 sticky top-0 z-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="flex items-center space-x-2">
            <div className="text-2xl font-bold accent-text">Optifi</div>
          </Link>

          <InteractiveNavigation
            isAuthenticated={isAuthenticated}
            userRole={userRole}
            publicLinks={publicLinks}
            userLinks={userLinks}
            adminLinks={adminLinks}
          />
        </div>
      </div>
    </nav>
  );
}
